<template>
	<view class="shopList_con">
		<view class="list">
			<view class="items flex" @tap="linkTo" v-for="(item, index) in recommendList" :key="index">
				<image :src="item.resUrl" mode="" class="shopImg"></image>
				<view class="right">
					<view class="tit">{{item.title}}</view>
					<view class="area">
						<text>{{item.streetTitle}}</text>
						<text class="xian"></text>
						<text>{{item.showAddress}}</text>
					</view>
					<view class="rent">
						<text class="rental">{{item.rent}}</text>
						<text class="size">{{item.floorSpace}}㎡</text>
					</view>
					<view class="tag">
						<span v-for="(tag, index1) in item.topTagList" :key="index1">{{tag.title}}</span>
						
					</view>
				</view>
			</view>
			
		</view>
		<view class="shangla">{{loadMore}}</view>
	</view>
</template>

<script>
	export default {
		onLoad() {
			this.token = uni.getStorageSync('userInfo').token
			this.getRecommendList()
		},
		data() {
			return {
				token: '',
				beg: false, //控制发请求
				loadMore:'-上拉加载更多-',
				isloadMore: false,
				start: 0,
				limit: 10,
				recommendList: []
			};
		},
		methods:{
			onReachBottom() {
				this.getRecommendList();
			},
			linkTo(){
				uni.navigateTo({
					url: '../businessShopDetils/businessShopDetils'
				})
			},
			// 推荐列表
			getRecommendList() {
				if (this.beg || this.isloadMore) return
				this.beg = true
				let params = {
					isRes: '1',
					start: this.start,
					limit: this.limit
				}
				uni.showLoading({
					title: '加载中'
				});
				this.$http.post(this.$api.getRecommendList, {
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res => {
					if (res.data.code == 0) {
						this.recommendList = [...this.recommendList, ...res.data.data.list]
						this.start += 10
						let total = this.start;
						if (!res.data.data.list.length || total >= res.data.data.totalNum || res.data.data.totalNum <= this.limit) {
							this.isloadMore = true;
							this.loadMore = '没有更多数据了';
						}
						this.beg = false
						uni.hideLoading();
					} else {
						this.beg = false
						this.toast(res.data.msg)
					}
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.shopList_con {
		width: 750rpx;

		.list {
			padding: 0 36rpx;
			box-sizing: border-box;

			.items {
				padding: 32rpx 0;
				box-sizing: border-box;
				border-bottom: 2rpx solid rgba(238, 238, 238, 1);

				.shopImg {
					width: 270rpx;
					height: 200rpx;
					border-radius: 10rpx;
				}

				.right {
					flex: 1;
					margin-left: 36rpx;

					.tit {
						font-size: 32rpx;
						font-family: PingFang SC;
						font-weight: 500;
						color: #343434;
					}

					.area {
						margin-top: 24rpx;
						text {
							font-size: 24rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #909090;
							&:nth-child(3){
								margin-right: 11rpx;
							}
						}
						.xian{
							margin: 0 12rpx 0;
							width: 2rpx;
							height: 25rpx;
							background: #909090;
						}

					}
					.rent{
						margin-top: 26rpx;
						.rental{
							font-size: 30rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #FF6634;
						}
						.size{
							margin-left: 24rpx;
							font-size: 24rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #909090;
						}
					}
					.tag{
						margin-top: 24rpx;
						span{
							margin-right: 10rpx;
							border: 2rpx solid #302E38;
							border-radius: 8rpx;
							padding: 9rpx 11rpx;
							box-sizing: border-box;
							font-size: 20rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #302E38;
						}
					}
				}
			}
		}
	}
</style>
